}

@keyframes radar {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  25% {
    opacity: .5;
    transform: scale(1);
  }

  50% {
    opacity: 1;
    transform: scale(2);
  }

  75% {
    opacity: .5;
    transform: scale(3);
  }

  100% {
    opacity: 0;
    transform: scale(4);
  }
}

